#home{
 height: 100vh;
 width: 100vw;
 max-width: 75rem;
 margin: 0 auto;
 overflow: hidden;
 position: relative;
}

.homeText{
    height: 100vh;
    width: 100vw;
    padding: 1.2rem;
    margin-top: 1rem;
    font-size: 1.4rem;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    justify-content:top;  
    /*text-shadow: 0.5px 0.5px 0 white;*/ 


}

.hello{
    font-size: 1.6rem;
    color: #dc143c;
    font-weight: 300;
}

.span1{
  font-weight: 400;
  font-size: 1.3rem;
}
.span2{
    font-weight: 500;
    font-size: 1.4rem;
  }

.dp{
    position: absolute;
    top: 3rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
    }

.line{
    margin-top: 0.9rem;
    margin-bottom: 0.9rem;
    width: 30%;
    border: 1px solid lightgrey;
}

.arrow{
    font-size: 0.8rem;
}


.name{
    /*height: 100vh;
    width: 100vw;
    /*padding: 2rem;
    overflow: hidden;
    display: inline; 
    top: 20rem;
    font-family: 'verdana', sans-serif;
    
    */
    font-size: 5rem;
    font-weight: 750;
    text-align: left;
    line-height: 0.75;
    margin-top: 200px;
    font-family: 'verdana', sans-serif;
    opacity: 1;
}



@media screen and (max-width:840px) {
    .dp{
        right: -25vw;
        height: 100vh;
        overflow: hidden;

    }
    .homeText{
        font-size: 5vw;
    }
    .name{
        margin-top: 200px;
    }
}