#skills{
    margin: 0 auto;
  
    overflow: hidden;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;  
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
}

.skillheader{
    margin-bottom: 10px;
  
}
.quote{
    font-family: 'Tienne', serif;
    font-weight: 400;
    font-size: 1.4rem;  
}
.author{
    font-family: 'Tienne', serif;
    font-size: 0.75rem;
    margin-top: 8px;
}

.skillcards{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
}

.skill{
    margin-right: 35px;
}

.skillcard1{
    text-align:center;
    
    margin-bottom: 50px;
    line-height: 1.6rem;
}
.skillcard2{
    text-align: center;
    
}


.tech{
    margin-bottom: 20px;
}

.skillrow{
    display: flex;
    justify-content: space-between;
    

}

.stars{
    font-size: 1.3rem;
    color: white;
    -webkit-text-stroke-width: 1px;   
    -webkit-text-stroke-color: white;
    
}

.star{
    padding-right: 3px;
}

.checked{
    color: white;
   /* text-shadow:0px 0px 3px white;*/
}
.unchecked{
    color: black;
}

.ability{
    width: 100vw;
    max-width: 92vw;
    font-family: 'Roboto', sans-serif;
    font-size: 2.2rem;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: 200;
}


        

 .list{
    margin: 20px 0 40px 0;

 } 

ul {
    line-height: 1.8em;

    margin-left: 0;
    padding: 0;

  }
  
  li {
    list-style: none;
    position: relative;
    padding: 0;
  }
      


@media screen and (max-width:480px) {
  
    .stars{
        margin-left: 10px;
    }
    .skillheader{
        margin-bottom: 30px;
        margin-top: 80px;
        text-align: center;
        
    }
    .ability{
        margin-bottom: 4.5vh;
        font-size: 2.2rem;
    }
    .skillcard1{
        margin-right: 0;
        
    }

    
}