#contact{
    margin: 0 auto;

    overflow: hidden;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-evenly;
    text-align: center;
    padding: 0 1rem; 
    
}

.title{
    font-size: 2.2rem;
    font-family: 'Roboto', sans-serif;
    font-weight: 200;
}

.contactform{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80vw;
    max-width: 60rem;
    margin-bottom: 3rem;
}

.icon{
    
    padding: 0px 80px 0px 30px;
    font-size: 40px;
    width: 30px;
    text-align: center;
    text-decoration: none;
}

.icon:hover{
    transform: scale(1.2);
}

.fa-github,.fa-twitter,.fa-instagram,.fa-linkedin{

  color: white;
  background: transparent;
}

.formname,.email,.msg{
    font-size: medium;
    width: 100%;
    max-width: 30rem;
    margin: 0.5rem;
    padding: 0.3rem 0.5rem;
    color: white;
    border: 1px solid grey;
    border-radius: 0.3rem;
    
}

.btnspan{
    color: black;
}

.submitbtn{
    background-color:white;

    padding: 0.5rem 3rem;
    margin-top: 1.5rem;
    border-radius: 0.4rem;
    font-size: 1rem;
     font-weight: 300;
}

.formnimg{
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.skyline{
   height: 110px;
   width: 280px;
}

.skyline img{
    object-fit: cover;
    width: 100%;
    max-height: 100%;
}

.iconrow{
    margin-top: 30px;
    margin-bottom: 30px;
}

.iconline {
    
    color:grey;
}

.copyright{
font-size: 0.8rem;
color: grey;
font-family: 'Roboto', sans-serif;

}

@media screen and (max-width:480px) {
     .skyline{
        height: 110px;
        width: 260px;
     }
     .icon{
    padding: 0px 50px 0px 15px;
     }
     .iconrow{
        margin-bottom: 35px;
     }
     
}