#journey{
    margin: 0 auto;

    overflow: hidden;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
    font-weight: 300;
   
}

.cards{
    
    display: flex;
    flex-wrap: wrap;
    max-width: 60rem;
    justify-content: center;
    align-items: center;
    font-family: 'roboto', sans-serif;
    font-weight: 200;
    margin-top: 5vh;
    font-size: 2rem;
}

.card{
    
    height: 12rem;
    width: 12rem;
    text-align: left;
    margin: 3px 10px 3px 10px ;

    
}

.journeytitle{
  
    font-family: 'roboto', sans-serif;
    font-size: 2.2rem;
    font-weight: 200;
    margin-right: 1.8rem;
    margin-bottom: 5vh;
    
}

.waterloo{
    margin: 0px 60px 0px 60px;
}

.cardline{
    margin-bottom: 1rem;
    border: 0;
    height: 1.6px;
    background: white;
    
}


.location{
    margin-bottom: 2rem;
    font-size: 1.5rem;
}

.cardtext{
    margin: 0.5rem 0;
}

.cardtextall{
    font-size: 1.2rem;
   } 

@media screen and (max-width:480px){
   .cardtextall{
    font-size: 0.85rem;
   } 
   .cards{
    margin-top: 2vh;
   }
   .cardtext{
    margin: 0.5rem 0;
   }
   .journeytitle{
       margin-top: 40px;
    }
    
}